
import React, { Component,useEffect} from 'react';
import Box from '@material-ui/core/Box';
import { Grid, Nav, Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import MainView from './MainView';
import SideBar from './sideBar';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import NativeSelect from '@material-ui/core/NativeSelect';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import CssBaseline from '@material-ui/core/CssBaseline';

import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Dialog from '@material-ui/core/Dialog'
import Question from './AddQues'

const useStyles = makeStyles((theme) => ({
  paper: {
    display: 'flex',
    width: '100%',
    flexDirection: 'column',
    alignItems: 'left',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  buttonAdd: {
    margin: theme.spacing(3, 0, 2),
    position: 'relative',
    display:'flex',
    float:'right',
    width:100,
    ['@media (max-width:600px)']: { // eslint-disable-line no-useless-computed-key

    }
  },
  card:{
    marginTop:10,
    width:'500px',
    ['@media (max-width:900px)']: { // eslint-disable-line no-useless-computed-key
      width:'100%'
    }
  },
  header:{
    borderBottom: '2px grey solid',
  }
}));

export interface SimpleDialogProps {
  open: boolean;
  selectedValue: string;
  count: integer;
  onSave: (value:string) => void;
  onClose: (value: string) => void;
  question: Question;
  isEditing:boolean;
}

function SimpleDialog(props: SimpleDialogProps) {
  const { onClose, selectedValue, open, onSave, count, questionToEdit, isEditing } = props;
//  const { onAddNew, ques } = props;
  const[isOpen, setOpen]=React.useState(props.open)
  console.log(open+" "+count+" "+isEditing);
  console.log(questionToEdit)
  const classes = useStyles();
  const [state, setState] = React.useState({
    category: props.category,
    topic: props.topic,
    ques:[],
  });
  const [currQues, setCurrQues] = React.useState({
      questionText:"",
      option1:"",
      option2:"",
      option3:null,
      option4:null,
      answer:"",
      id:''
  })
  console.log("isOpen "+isOpen)
  if(isEditing===true&&isOpen===false){
    setOpen(true);
  }
  useEffect(() => {
  // Your code here
  console.log("inside use effect")
  if(isEditing)
  {
    console.log("editing question")
    setCurrQues({
      questionText:questionToEdit.questionText,
      option1:questionToEdit.option1,
      option2:questionToEdit.option2,
      option3:questionToEdit.option3,
      option4:questionToEdit.option4,
      answer:questionToEdit.answer,
      id:questionToEdit.id
    })
  }
  else{
    console.log("not editting")
    setCurrQues({
      questionText:"",
      option1:"",
      option2:"",
      option3:null,
      option4:null,
      answer:"",
      id:''
    })
  }
}, [isOpen]);

  const [alert, setAlert] = React.useState(false)
  const [answerNotMatchingAlert, setAnswerNotMatchingAlert] =  React.useState(false)
  //debugger;
  let  question={
    questionText:"",
    option1:"",
    option2:"",
    option3:null,
    option4:null,
    answer:"",
    id:''
   }

 const handleOptionSelect=(value)=>{
   console.log("found Value "+value)
   this.setState({
     id : value
   })
 }
 const handleCategoryChange=(event,value)=>{
   console.log(value)

   setState({
     ques:[],
     totalMarks:0,
     activeId:'',
     isAdding:false
   },()=>{
     console.log("category is "+state.category)
   })
 }
 const handleTopicChange = (event)=>{
   console.log("setting topic to "+event.target.value);
   setState({
       category: state.category,
       topic: event.target.value
   })
 }

 const handleQuestionChange=(e)=>{
   console.log(e.target.value)
   question.questionText = e.target.value;
   console.log(state.ques.length+1)

   setCurrQues({
     ...currQues,
     questionText:e.target.value,
     id:count
   })
 }
 const handleOption1Change=(e)=>{
   question.option1 = e.target.value;
   console.log(question.option1)
   setCurrQues({
     ...currQues,
     option1:e.target.value,
     id:count
   })
 }
 const handleOption2Change=(e)=>{
   question.option2 = e.target.value;
   console.log(question.option2)
   setCurrQues({
     ...currQues,
     option2:e.target.value,
     id:count
   })
 }
 const handleOption3Change=(e)=>{
   question.option3 = e.target.value;
   console.log(question.option2)
   setCurrQues({
     ...currQues,
     option3:e.target.value,
     id:count
   })
 }
 const handleOption4Change=(e)=>{
   question.option4 = e.target.value;
   console.log(question.option2)
   setCurrQues({
     ...currQues,
     option4:e.target.value,
     id:count
   })
 }
 const handleAnswerChange=(e)=>{
   question.answer = e.target.value;
   console.log(question.answer)
   setCurrQues({
     ...currQues,
     answer:e.target.value,
     id:count
   })
 }

 const isValid=(ques)=>
 {

   if(ques.questionText===""||
       ques.option1===""||
       ques.option2===""||
       ques.answer==="")
       {
         console.log(ques.questionText+" "+
         ques.option1+ " "+
         ques.option2+ " "+
         ques.answer
         )
         setAnswerNotMatchingAlert(false);
         setAlert(true);
         return false;
       }
   if(ques.answer!=ques.option1
     &&ques.answer!=ques.option2
     &&ques.answer!=ques.option3
     &&ques.answer!=ques.option4)
       {
         setAnswerNotMatchingAlert(true);
         setAlert(false);
         return false;
       }
   setAnswerNotMatchingAlert(false);
   setAlert(false);
   return true;
 }

 const handleAdd = (event)=>{
   console.log(currQues)
   if(!isValid(currQues))
   {
     console.log(alert+" "+answerNotMatchingAlert);
     return;
   }
   else {
     setAlert(false)
     setAnswerNotMatchingAlert(false);
     let ques = currQues
     setCurrQues({});
     setOpen(false);
     onSave(ques, count)
     // console.log(currQues)
     // question.id = state.ques.length+1;
     //  setState({
     //    ques:[...state.ques, currQues]
     //  },()=>{
     //     console.log(state.ques)
     //  })
     //  question={
     //    questionText:"",
     //    option1:"",
     //    option2:"",
     //    answer:"",
     //    id:'',
     //    marks:0
     //  }
     //  setCurrQues({
     //    questionText:"",
     //    option1:"",
     //    option2:"",
     //    answer:"",
     //  })

   }

 }


  const handleClose = () => {
    setOpen(false);
    onClose(selectedValue);
  };

  const handleListItemClick = (value: string) => {
    onClose(value);
  };


  return (
    <Dialog onClose={handleClose} open={open}>
    <Card className={classes.card}>
     <CardContent>
      <FormControl onSubmit={handleAdd} style={{width:'100%'}}>
        {alert===true?<p style={{color:"red"}}>Some inputs are invalid.</p>:""}
        {answerNotMatchingAlert===true?<p style={{color:"red"}}>Answer is not matching to any of the options.</p>:""}

        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          name="question"
          label="question"
          id="question"
          value = {currQues.questionText}
          onChange = {e => {handleQuestionChange(e)}}
        />
        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          name="option1"
          label="option1"
          id="option1"
          value = {currQues.option1}
          onChange = {e => {handleOption1Change(e)}}
        />
        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          name="option2"
          label="option2"
          id="option2"
          value = {currQues.option2}
          onChange = {e => {handleOption2Change(e)}}
        />
        {

        }

        <Button variant="text">
         + Add more options
        </Button>
        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          name="answer"
          label="answer"
          id="answer"
          value = {currQues.answer}
          onChange = {e => {handleAnswerChange(e)}}
        />
        <Grid container item md={12} display="flex">
        <Button
          type="Add"
          variant="contained"
          color="primary"
          onClick={handleAdd}
          style={{width:"50px",margin:"auto"}}
        >
         {isEditing===true?"Save":"Add"}
        </Button>
        <Button
          type="Add"
          variant="contained"
          color="red"
          onClick={handleClose}
          style={{width:"50px",margin:"auto"}}
        >
         Cancel
        </Button>
      </Grid>
      </FormControl>
      </CardContent>
     </Card>
    </Dialog>
  );
}
export default SimpleDialog
