
import React, { Component } from 'react';
import Box from '@material-ui/core/Box';
import { Grid, Nav, Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import MainView from './MainView';
import SideBar from './sideBar';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import NativeSelect from '@material-ui/core/NativeSelect';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import CssBaseline from '@material-ui/core/CssBaseline';

import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import SimpleDialog from './AddQuesDialog';


import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";



const categories = [
  { title: 'Computer Science', value:"cse" },
  { title: 'Science', value: "science" },
  { title: 'Maths', value:"maths" },
  { title: 'General Knowledge', value: "gk" },
  { title: 'Other', value: "other" }
]
const useStyles = makeStyles((theme) => ({
  paper: {
    display: 'block',
    margin:"auto",
    padding:"auto"
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  buttonAdd: {
    margin: theme.spacing(3, 0, 2),
    position: 'relative',
    display:'flex',
    float:'right',
    width:100,
    ['@media (max-width:600px)']: { // eslint-disable-line no-useless-computed-key

    }
  },
  card:{
    marginTop:10,
    width: '100%',
    ['@media (max-width:900px)']: { // eslint-disable-line no-useless-computed-key
      width:'100%'
    }
  },
  header:{
    borderBottom: '2px grey solid',
  },
  icons:{
    display:"block",
    margin:"auto",
    padding:"auto",
    ['@media (max-width:600px)']: { // eslint-disable-line no-useless-computed-key
      display:"flex"
    }
  }
}));

export interface Question {
  id: string;
  questionText: string;
  option1: string;
  option2: string;
  option3: string;
  option4: string;
  answer: string;
}


export default function AddQues(props) {

   const classes = useStyles();
   const [state, setState] = React.useState({
     category: props.category,
     topic: props.topic,
     ques:[],
   });
   const [count, setCount] = React.useState(0);
   const [questions, setQuestions] = React.useState([]);

  const emails = ['username@gmail.com', 'user02@gmail.com'];
  const [open, setOpen] = React.useState(false);
  const [selectedValue, setSelectedValue] = React.useState(emails[1]);
  const [editing, setEditing] = React.useState({
    isTrue: false,
    index:0
  });
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleEdit=(e,index)=>{
    console.log("index to edit "+index)
    console.log(state.ques[index])
    setEditing({
      isTrue:true,
      index:index
    })

      setOpen(true);
    console.log("editing")
  }

  const handleDelete=(e,index)=>{
    console.log("Deleting "+index)
    let q = state.ques;
    console.log(q)
    q.splice(index,1)
    console.log(q)
    setState({
      ...state,
      ques:q
    })
  }

  const handleClose = (value: string) => {
    setOpen(false);
    setSelectedValue(value);
    setEditing({
      isTrue:false,
      index:null
    })
  };

  const handleSave=(value:Question, index:int)=>{
    let q = state.ques;
    console.log("updating...")
    console.log(q[index])
    q[index] = value
    console.log(value)
    console.log()
    setState({
      ...state,
      ques:q
    })
    setEditing({
      ...editing,
      isTrue:false
    })
    setOpen(false)
  }

  const handleAddNewQuestion = (value:Question) =>{
    console.log("found")
    setCount(count+1)
    console.log(count)
    console.log(value)
    setState({
      ...state,
      ques:[...state.ques,value]
    })
    setOpen(false)
  }
  const[submitted, setSubmitted]=React.useState(false);
  const handleSubmit=()=>{
    setSubmitted(true);
  }
  if(submitted){
    return(
      <Grid className={classes.root} container item xs={12} alignItems="center" justifyContent="center">
      <Grid container item xs={12} alignItems="center" justifyContent="center">There was some problem adding this quiz.</Grid>
      <Grid><Link to="/">Go Home.</Link></Grid>
      </Grid>)
  }



    return (
    <div>

    <Container component="main">

    <div className={classes.paper}>
    <Grid item container sm={12} style={{display:"flex",margin:"auto", borderBottom:"2px solid grey",marginTop:"20px"}}>
    <Grid container item sm={6} style={{justify:"space-between"}}>
    <Typography  variant="h6"  component="div">
      Category: {props.category}
    </Typography>
    </Grid>

    <Grid  container item sm={6} style={{float:"right"}}>
    <Typography  variant="h6"  component="div">
      Topic: {props.topic}
    </Typography>
    </Grid>
    </Grid>

    <div>

      <br />
      <Button variant="outlined" onClick={handleClickOpen}>
        Add Question
      </Button>
      <SimpleDialog
        selectedValue={selectedValue}
        open={open}
        onClose={handleClose}
        onSave={editing.isTrue===true?handleSave:handleAddNewQuestion}
        count = {editing.isTrue===true?editing.index:count}
        questionToEdit={editing.isTrue===true?state.ques[editing.index]:{}}
        isEditing={editing.isTrue}
      />
    </div>

    {
      state.ques.map((q,index)=>{
        return(
          <Grid container item sm={12} style={{display:"flex"}}>
          <Grid container item sm={9}>
          <Card style={{margin:"5px", maxWidth:'100%', width:'600px'}}>
           <CardContent>
          <h5>{index+1}. {q.questionText}</h5>
          a. {q.option1}
          <br/>b. {q.option2}
          <br/>ans: {q.answer}
          </CardContent>
          </Card>
          </Grid>
          <Grid container item sm={3} className={classes.icons}>
          <Grid><Button onClick={(event) => handleEdit(event, index)}><EditIcon  fontSize="large"/></Button></Grid>
          <Grid><Button onClick={(event) => handleDelete(event, index)} ><DeleteIcon fontSize="large"/></Button></Grid>
          </Grid>
          </Grid>
        )
      })
    }

      </div>
    </Container>
    {
      state.ques.length>0?
      <Grid container item xs={12} justify="center" style={{marginTop:"50px"}}>
      <Button
      variant="contained"
      color="primary"
      className={classes.SubmitButton}
      onClick={handleSubmit}>Submit
      </Button>
      </Grid>:
      ""
    }
    </div>
    );
}
